:root {
  --bg: #F2E0E9;
  ;
  --panel: #FAEFF4;
  --ink: #16070E;
  --ink-weak: #16070e20;
  --primary: #E72787;
  --primary-strong: #E72787;
  --tile-bg: #FAEFF4;
  --muted: #88767E;
  --outline: #16070E;
  /* Shadows */
  --shadow: 0px 0px 0 var(--ink), 4px 4px 0 var(--ink-weak);
  --shadow-strong: 3px 3px 0 var(--ink), 6px 6px 0 var(--ink-weak);
  --transform-shadow: translate(-2px, -2px);
  --border: var(--bd-1) solid var(--outline);



  /*Text Outlines*/
  --text-outline: 1px 0 var(--ink),
    -1px 0 var(--ink),
    0 1px var(--ink),
    0 -1px var(--ink),
    1px 1px var(--ink),
    -1px 1px var(--ink),
    1px -1px var(--ink),
    -1px -1px var(--ink);

  /* Radii */
  --r-sm: 12px;
  --r-md: 16px;
  --r-lg: 24px;
  /* Spacing scale */
  --space: 12px;
  --space-sm: 8px;
  --space-lg: 16px;
  --space-xs: 6px;
  /* Borders */
  --bd-1: 2px;
  --bd-2: 4px;
  /* Motion */
  --t-fast: 120ms ease-in-out;
  --t-slow: 200ms ease-in-out;

  --fonts: "Montserrat", sans-serif;
  --font-light: 400;
  --font-regular: 700;
  --font-bold: 900;
  --to-bold: "wght" 900;

  /* Shared transition tokens */
  --u-transition: transform var(--t-fast),
    box-shadow var(--t-fast),
    outline-color var(--t-fast),
    background-color var(--t-fast),
    font-variation-settings var(--t-slow);
}

/* Transitions & motion */
.u-transition {
  transition: var(--u-transition);
}



.title-text {
  line-height: 0.85;
  font-weight: 400;
  font-size: 37pt;
  color: var(--panel);
  text-shadow: var(--text-outline), var(--shadow-strong);
  letter-spacing: 1px;
  font-family: "duckie", sans-serif;
}

@keyframes fade-in {
  from {
    opacity: 0;
    transform: scale(0.98);
  }

  to {
    opacity: 1;
    transform: scale(1);
  }
}

@media (prefers-reduced-motion: reduce) {
  .u-transition {
    transition: none;
  }

  * {
    animation: none !important;
  }
}

/* Utilities: radius */
.u-r-sm {
  border-radius: var(--r-sm);
}

.u-r-md {
  border-radius: var(--r-md);
}

.u-r-lg {
  border-radius: var(--r-lg);
}

/* Utilities: shadows */
.u-shadow-1 {
  box-shadow: var(--shadow);
}

.u-shadow-2 {
  box-shadow: var(--shadow-strong);
}

/* Utilities: borders */
.u-border-1 {
  border: var(--bd-1) solid var(--outline);
}

.u-border-2 {
  border: var(--bd-2) solid var(--outline);
}

/* Utilities: colors */
.u-bg-panel {
  background: var(--panel);
}

.u-bg-tile {
  background: var(--tile-bg);
}

.u-ink {
  color: var(--ink);
}

.u-primary-bg {
  background: var(--primary);
}

.u-primary {
  color: var(--primary);
}

/* Utilities: layout spacing */
.u-flex-col {
  display: flex;
  flex-direction: column;
}

.u-gap-sm {
  gap: var(--space-sm);
}

.u-gap {
  gap: var(--space);
}

.u-gap-lg {
  gap: var(--space-lg);
}